@import "_partial/_config";

/* background */
.accessMap,
.accessAreainfo,
.accessTheatre {
	background: url(/img/img01.jpg) no-repeat center center;
	background-size: cover;
	background-attachment: fixed;
}

/* ################# accessMap ################# */
#maps {
	padding-top: 16px;
	.overview {
		padding-top: 16px;
		.imgTxtBox {
			@include imgTxtBox(48%, 50%);
			.imgBox {
				img {
					width: 100%;
				}
			}
			.txtBox_name {
				font-weight: normal;
				@include fontsize(20);
			}
			.txtBox_body {
				padding-top: 16px;
				@include fontsize(12);
			}
		}
	}
	.map {
		padding-top: 64px;
		img {
			width: 100%;
		}
	}
	.gMap {
		position: relative;
		margin-top: 64px;
		padding-top: 50%;
		iframe {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}
	}
}

@media screen and (max-width: 768px){
	#maps {
		padding-top: 16px;
		.overview {
			.imgTxtBox {
				.txtBox {
					float: none;
					width: 100%;
				}
				.imgBox {
					float: none;
					width: 100%;
					img {
						width: 100%;
					}
				}
				.txtBox_name {
					margin-top: 16px;
					font-weight: normal;
					@include fontsize(20);
				}
				.txtBox_body {
					padding-top: 16px;
					@include fontsize(12);
				}
			}
		}
		.map {
			padding-top: 64px;
			img {
				width: 100%;
			}
		}
		.gMap {
			position: relative;
			margin-top: 64px;
			padding-top: 50%;
			iframe {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
			}
		}
	}
}

/* ################# areainfo ################# */
#areainfo {
	padding-top: 16px;
	.inns,
	.areainfo {
		&_title {
			margin-top: 16px;
			@include fontsize(24);
			text-align: center;
			text-decoration: underline;
		}
		&Box {
			width: 45%;
			float: left;
			margin: 32px 0 64px;
			&:nth-child(even) {
				float: right;
			}
			&_name {
				margin-top: 16px;
				text-align: center;
			}
			&_body {
				margin-top: 16px;
			}
		}
	}
}
@media screen and (max-width: 768px) {
	#areainfo {
		.inns,
		.areainfo {
			&_title {
				@include fontsize(20);
			}
			&Box {
				width: 100%;
				float: none;
				margin: 32px 0;
				&:nth-child(even) {
					float: none;
				}
				&_img {
					img {
						width: 100%;
					}
				}
				&_body {
					line-height: 1.4;
					@include fontsize(12);
				}
			}
		}
	}
}

/* ################# theatre ################# */
#theatre {
	padding-top: 16px;
	.building {
		margin-top: 64px;
		&_title {
			text-align: center;
			@include fontsize(24);
		}
		.txtImgBox {
			margin-top: 32px;
			@include txtImgBox(50%, 45%);
		}
		.imgBox {
			img {
				width: 100%;
			}
		}
		.txtBox {
			line-height: 1.4;
			@include fontsize(14);
			&_body {
				margin: 1em;
				&:first-child {
					margin-top: 0;
				}
			}
		}
	}
}

@media screen and (max-width: 768px) {
	#theatre {
		.building {
			margin-top: 32px;
			.txtImgBox {
				margin-top: 16px;
				.imgBox {
					float: none;
					width: 100%;
				}
				.txtBox {
					float: none;
					width: 100%;
				}
			}
			.txtBox_body,
			.txtBox_body:first-child {
				margin: 1em 0 0;
			}
		}
	}
}
